
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  list-style: none;
}
.main-box {
  max-width: 1120px;
  min-height: 700px;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 15px 30px;
  margin: 50px auto 0;
}
.main-box h2 {
  text-align: center;
}

header {
  width: 100%;
  padding: 20px 5%;
  background: #333;
}
header h1 {
  font-weight: normal;
}
header a {
  text-decoration: none;
  color: #fff;
}
header h1 a {
  color: #ddd;
}
header ul {
  padding: 20px 0 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
header ul a {
  font-weight: bold;
}

footer {
  background: #333;
  padding: 30px 5%;
  margin-top: 50px;
}
footer h3 {
  color: #ddd;
  font-size: 120%;
  margin-bottom: 20px;
}
.copy {
  text-align: center;
  color: #ddd;
  font-size: 90%;
}

.done { text-decoration: line-through;
}
.todos .input-box {
  margin-bottom: 20px;
}
.todos .input-box input[type=text] {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.todos ul li {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
}
.time {
  text-decoration: none;
}
.todos button {
  padding: 15px;
  background: #000;
  border: none;
  border-radius: 5px;
  color: #ddd;
  cursor: pointer;
  margin: 15px auto;
  display: block;
}




















